import React from "react";
import { HashLink } from "react-router-hash-link";
import { Helmet } from "react-helmet-async";

const Orthodontics = () => {
  return (
    <div className="container py-5 mt-5">
      {/* SEO & OG Tags */}
      <Helmet>
        <title>
          تقويم الأسنان – ابتسامة صحية وجميلة | مركز سليم سمايل – رام الله
        </title>
        <meta
          name="description"
          content="تقويم الأسنان يساعد على تحسين وضع الأسنان والفكين للحصول على ابتسامة صحية وجميلة. تعرف على الأنواع، المزايا، العيوب، ومدة العلاج في مركز سليم سمايل – رام الله."
        />
        <meta
          name="keywords"
          content="تقويم الأسنان, ابتسامة, علاج الأسنان, تجميل الأسنان, تقويم شفاف, تقويم معدني, تقويم داخلي, تقويم خزفي"
        />

        {/* OG Tags */}
        <meta
          property="og:title"
          content="تقويم الأسنان – ابتسامة صحية وجميلة | مركز سليم سمايل – رام الله"
        />
        <meta
          property="og:description"
          content="تعرف على أنواع تقويم الأسنان (المعدني، الشفاف، الخزفي، الداخلي) وفوائده ومدة العلاج مع فريق متخصص في مركز سليم سمايل – رام الله."
        />
        <meta
          property="og:image"
          content="https://via.placeholder.com/800x450.png?text=Orthodontics+Dental+Care"
        />
        <meta property="og:type" content="article" />
        <meta property="og:locale" content="ar_AR" />

        {/* FAQ Schema */}
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "FAQPage",
            mainEntity: [
              {
                "@type": "Question",
                name: "ما هو تقويم الأسنان؟",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "تقويم الأسنان هو علاج طبي يهدف إلى تصحيح وضع الأسنان والفكين لعلاج مشاكل مثل تزاحم الأسنان، سوء الإطباق، وبروز الأسنان الأمامية.",
                },
              },
              {
                "@type": "Question",
                name: "ما هي فوائد تقويم الأسنان؟",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "يساعد على تحسين الإطباق والعضّة، تعزيز صحة اللثة، تحسين النطق والمضغ، الوقاية من مشاكل مفصل الفك، وزيادة الثقة بالنفس.",
                },
              },
              {
                "@type": "Question",
                name: "ما هي أنواع تقويم الأسنان؟",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "تشمل الأنواع: التقويم المعدني التقليدي، التقويم الشفاف (Invisalign)، التقويم الخزفي، والتقويم الداخلي (Lingual Braces).",
                },
              },
              {
                "@type": "Question",
                name: "كم تستغرق مدة علاج تقويم الأسنان؟",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "عادة ما تتراوح المدة بين 12 إلى 24 شهرًا حسب الحالة. الحالات البسيطة قد تحتاج وقتًا أقل، خاصة مع التقويم الشفاف.",
                },
              },
              {
                "@type": "Question",
                name: "متى يُنصح ببدء تقويم الأسنان؟",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "للأطفال يُفضل البدء من عمر 7 سنوات، وللبالغين يمكن البدء في أي وقت طالما أن الأسنان واللثة بصحة جيدة.",
                },
              },
            ],
          })}
        </script>
      </Helmet>

      {/* Page Content */}
      <h1 className="mb-4 text-center" style={{ color: "#00b3ff" }}>
        تقويم الأسنان
      </h1>
      <h5 className="text-center mb-5">ابتسامتك تبدأ من هنا</h5>
      <p className="text-center">
        مركز سليم سمايل – رام الله | ابتسامة متكاملة تبدأ من هنا
      </p>
      <p>
        تقويم الأسنان هو فرع متخصص من طب الأسنان يهدف إلى تصحيح وضع الأسنان
        والفكّين، وذلك لتحسين الوظيفة الفموية والمظهر الجمالي للابتسامة. يساعد
        تقويم الأسنان على علاج مشاكل عدة مثل تزاحم الأسنان، سوء الإطباق، وبروز
        الأسنان الأمامية.
      </p>

      <h2 className="mt-4">ما هو تقويم الأسنان؟</h2>
      <p>يُستخدم تقويم الأسنان لعلاج مشاكل مثل:</p>
      <ul>
        <li>تزاحم الأسنان أو تباعدها.</li>
        <li>سوء الإطباق (انحراف الفك أو العضة).</li>
        <li>بروز الأسنان الأمامية.</li>
        <li>مشاكل في النطق أو المضغ.</li>
      </ul>

      <h2 className="mt-4">فوائد تقويم الأسنان</h2>
      <ul>
        <li>تحسين الإطباق والعضّة.</li>
        <li>تعزيز صحة اللثة والأسنان.</li>
        <li>تحسين النطق والمضغ.</li>
        <li>الوقاية من مشاكل مفصل الفك (TMJ).</li>
        <li>رفع الثقة بالنفس والمظهر العام.</li>
      </ul>

      <h2 className="mt-4">أنواع تقويم الأسنان</h2>

      <h3 className="mt-3">1. التقويم المعدني التقليدي</h3>
      <p>
        الأكثر شيوعًا وفعالية في الحالات المتقدمة. يتميز بقدرته على تصحيح
        المشكلات المعقدة بتكلفة معقولة، لكنه يكون ظاهرًا بشكل واضح ويحتاج إلى
        عناية خاصة.
      </p>

      <h3 className="mt-3">2. التقويم الشفاف (Invisalign)</h3>
      <p>
        خيار مثالي للبالغين الذين يرغبون في علاج غير مرئي تقريبًا. يتميز بالراحة
        وسهولة الإزالة عند الأكل أو تنظيف الأسنان، لكنه غير مناسب للحالات
        المعقدة جدًا ويحتاج التزامًا كبيرًا من المريض.
      </p>

      <h3 className="mt-3">3. التقويم الخزفي (السيراميكي)</h3>
      <p>
        يشبه التقويم المعدني لكنه مصنوع من مواد خزفية بلون قريب من لون الأسنان،
        مما يجعله أكثر جمالية. ومع ذلك فهو أكثر عرضة للكسر وقد يتغير لونه بمرور
        الوقت.
      </p>

      <h3 className="mt-3">4. التقويم الداخلي (Lingual Braces)</h3>
      <p>
        يتم تركيبه خلف الأسنان ليكون غير مرئي من الخارج، مما يجعله خيارًا
        مثاليًا للمرضى الذين لا يفضلون الشكل التقليدي. لكنه أكثر تكلفة وقد يسبب
        بعض التهيج للسان في البداية.
      </p>

      <h2 className="mt-4">مدة علاج تقويم الأسنان</h2>
      <p>
        عادة ما تتراوح مدة العلاج بين 12 إلى 24 شهرًا. الحالات البسيطة قد تستغرق
        وقتًا أقل، خاصة عند استخدام التقويم الشفاف. أما الأطفال فقد يحتاجون إلى
        مرحلة أولية قبل البدء بالتقويم الثابت.
      </p>

      <h2 className="mt-4">متى يُنصح ببدء العلاج؟</h2>
      <p>
        للأطفال: يُفضل البدء في الفحص عند عمر 7 سنوات. للبالغين: يمكن البدء في
        أي عمر طالما أن اللثة والأسنان بصحة جيدة.
      </p>

      <h2 className="mt-4">العناية بالأسنان أثناء التقويم</h2>
      <ul>
        <li>تنظيف الأسنان بعد كل وجبة.</li>
        <li>استخدام فراشي وأسلاك خاصة.</li>
        <li>تجنب الأطعمة القاسية واللزجة.</li>
        <li>زيارة الطبيب بانتظام لضبط التقويم.</li>
      </ul>

      <h2 className="mt-4">لماذا تختار مركز سليم سمايل لتقويم الأسنان؟</h2>
      <ul>
        <li>استشارة شاملة باستخدام التصوير ثلاثي الأبعاد.</li>
        <li>خطة علاجية مخصصة لكل مريض.</li>
        <li>خيارات متنوعة وعصرية للتقويم.</li>
        <li>إشراف من فريق متخصص ذو خبرة.</li>
        <li>متابعة دقيقة لضمان أفضل النتائج.</li>
        <li>بيئة علاجية مريحة وآمنة.</li>
      </ul>

      <footer className="mt-4">
        <HashLink to="/#services" className="btn btn-outline-secondary">
          العودة للخدمات
        </HashLink>
      </footer>
    </div>
  );
};

export default Orthodontics;
