import React from "react";
import { HashLink } from "react-router-hash-link";
import { Helmet } from "react-helmet-async";

const RootCanalTherapy = () => {
  return (
    <div className="container py-5 mt-5">
      {/* ✅ Helmet SEO */}
      <Helmet>
        <title>
          علاجات العصب السني – إنقاذ السن من الداخل | مركز سليم سمايل – رام الله
        </title>
        <meta
          name="description"
          content="تعرف على خطوات ومميزات علاج العصب السني (Root Canal Therapy) في مركز سليم سمايل – رام الله بإشراف د. آية الفراحين، باستخدام أحدث التقنيات لإنقاذ الأسنان المصابة."
        />
        <meta
          name="keywords"
          content="علاج العصب, علاج جذور الأسنان, Root Canal Therapy, التهاب العصب, تسوس عميق, مركز سليم سمايل, رام الله"
        />

        {/* Open Graph */}
        <meta
          property="og:title"
          content="علاجات العصب السني – مركز سليم سمايل"
        />
        <meta
          property="og:description"
          content="اكتشف أحدث تقنيات علاج العصب السني في مركز سليم سمايل – رام الله مع د. آية الفراحين."
        />
        <meta property="og:type" content="article" />
        <meta
          property="og:image"
          content="https://slimsmile.ps/images/root-canal.jpg"
        />
        <meta
          property="og:url"
          content="https://slimsmile.ps/articles/root-canal-therapy"
        />

        {/* FAQ Schema */}
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "FAQPage",
            mainEntity: [
              {
                "@type": "Question",
                name: "ما هو علاج العصب (Root Canal Therapy)؟",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "هو إجراء طبي لإنقاذ الأسنان المصابة بالتهاب أو عدوى في العصب الداخلي، يتضمن إزالة العصب المصاب وتنظيف القنوات وحشوها لحماية السن.",
                },
              },
              {
                "@type": "Question",
                name: "متى نحتاج إلى علاج العصب؟",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "نحتاج لعلاج العصب عند وجود ألم حاد، حساسية شديدة، تغير لون السن، خراج أو تسوس عميق وصل إلى لب السن.",
                },
              },
              {
                "@type": "Question",
                name: "كم عدد الجلسات المطلوبة لعلاج العصب؟",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "معظم الحالات تُعالج في جلسة واحدة، بينما الحالات المعقدة قد تحتاج إلى جلستين أو ثلاث.",
                },
              },
            ],
          })}
        </script>
      </Helmet>

      <h1 className="mb-4 text-center" style={{ color: "#00b3ff" }}>
        علاجات العصب السني
      </h1>
      <h5 className="text-center mb-5"> الفرصه الاخيره قبل فقدان السن </h5>
      <p className="text-center text-muted">
        بقلم: د. آية الفراحين – أخصائية علاج جذور الأسنان
      </p>
      <p className="text-center text-muted">
        مركز سليم سمايل – رام الله | خبرة في علاج العصب بأحدث الأجهزة
      </p>

      <h2 className="mt-4">ما هو علاج العصب (Root Canal Therapy)؟</h2>
      <p>
        علاج العصب السني هو إجراء طبي يُستخدم لإنقاذ الأسنان المصابة بالتهاب أو
        عدوى في العصب الداخلي (اللب السني)، دون الحاجة إلى خلع السن. يتضمن
        العلاج إزالة العصب المصاب، وتنظيف الجذر، ثم حشو القنوات الداخلية بمادة
        خاصة لحمايتها من العدوى مجددًا.
      </p>
      <p>
        الهدف هو الحفاظ على السن الطبيعي ما أمكن، مع ضمان راحة المريض وجودة
        العلاج.
      </p>

      <h2 className="mt-4">متى نحتاج إلى علاج العصب؟</h2>
      <ul>
        <li>ألم حاد أو نابض في السن، خاصةً عند الضغط أو الأكل.</li>
        <li>حساسية شديدة للحرارة أو البرودة.</li>
        <li>تغيّر لون السن (تصبغ داكن).</li>
        <li>خراج أو انتفاخ في اللثة المجاورة للسن.</li>
        <li>تسوس عميق وصل إلى لب السن.</li>
      </ul>

      <h2 className="mt-4">خطوات علاج العصب في مركز سليم سمايل</h2>
      <ol>
        <li>
          تصوير شعاعي ثلاثي الأبعاد (CBCT أو X-ray) لتحديد طول الجذر وحالة
          العظم.
        </li>
        <li>تخدير موضعي لضمان راحة تامة.</li>
        <li>فتح التاج وتنظيف القنوات العصبية باستخدام أدوات دقيقة.</li>
        <li>تعقيم القنوات بموجات فوق صوتية ومحاليل مضادة للبكتيريا.</li>
        <li>حشو القنوات بمادة الجوتابيركا وإغلاق السن مؤقتًا أو دائمًا.</li>
        <li>ترميم السن لاحقًا بتاج أو حشوة تجميلية لحمايته من الكسر.</li>
      </ol>

      <h2 className="mt-4">مدة الجلسة وعدد الزيارات</h2>
      <ul>
        <li>معظم الحالات تُنجز في جلسة واحدة.</li>
        <li>الحالات المعقدة قد تحتاج إلى جلستين أو ثلاث جلسات.</li>
      </ul>

      <h2 className="mt-4">مميزات العلاج في مركزنا</h2>
      <ul>
        <li>تقنيات رقمية متقدمة لتحديد أطوال القنوات بدقة.</li>
        <li>أجهزة حديثة تقلل من زمن الجلسة.</li>
        <li>متابعة دقيقة لضمان شفاء الجذور وعدم تكرار العدوى.</li>
        <li>بيئة علاجية مريحة وتجربة بلا ألم.</li>
      </ul>

      <h2 className="mt-4">رسالة د. آية الفراحين</h2>
      <p>
        <em>
          "علاج العصب هو إنقاذ حقيقي للسن. نحرص على تقديم العلاج بأقل تدخل ممكن،
          باستخدام أحدث التقنيات وبأعلى درجات التعقيم والدقة. لأن الحفاظ على
          السن الطبيعي أولويتنا."
        </em>
      </p>

      <footer className="mt-4">
        <HashLink to="/#services" className="btn btn-outline-secondary">
          العودة للخدمات
        </HashLink>
      </footer>
    </div>
  );
};

export default RootCanalTherapy;
