import React from "react";
import { Helmet } from "react-helmet-async";
import { HashLink } from "react-router-hash-link";

const Veneers = () => {
  return (
    <div className="container py-5 mt-5">
      {/* ✅ Helmet SEO */}
      <Helmet>
        <title>
          القشور الخزفية (الفينير) – سر الابتسامة المثالية | مركز سليم سمايل –
          رام الله
        </title>
        <meta
          name="description"
          content="احصل على ابتسامة هوليودية طبيعية مع القشور الخزفية (الفينير) في مركز سليم سمايل – رام الله. حلول متقدمة لتصحيح شكل الأسنان ولونها بمواد عالية الجودة."
        />
        <meta
          name="keywords"
          content="فينير, القشور الخزفية, ابتسامة هوليودية, تجميل الأسنان, مركز سليم سمايل, رام الله, Veneers"
        />

        {/* Open Graph */}
        <meta
          property="og:title"
          content="القشور الخزفية (الفينير) – مركز سليم سمايل"
        />
        <meta
          property="og:description"
          content="تعرف على أنواع وخطوات القشور الخزفية (Veneers) مع مركز سليم سمايل – رام الله، سر الابتسامة المثالية."
        />
        <meta property="og:type" content="article" />
        <meta
          property="og:image"
          content="https://slimsmile.ps/images/veneers.jpg"
        />
        <meta
          property="og:url"
          content="https://slimsmile.ps/articles/veneers"
        />

        {/* FAQ Schema */}
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "FAQPage",
            mainEntity: [
              {
                "@type": "Question",
                name: "ما هي القشور الخزفية (Veneers)؟",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "القشور الخزفية هي عدسات تجميلية رقيقة توضع على الأسنان لتصحيح اللون والشكل والحجم، والحصول على ابتسامة طبيعية وجذابة.",
                },
              },
              {
                "@type": "Question",
                name: "متى أحتاج للفينير؟",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "يُنصح بالفينير عند وجود تصبغات لا تستجيب للتبييض، أو فراغات بين الأسنان، أو عدم تناسق في الشكل، أو تآكل الأسنان.",
                },
              },
              {
                "@type": "Question",
                name: "كم تدوم القشور الخزفية؟",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "مع العناية الجيدة يمكن أن تدوم القشور الخزفية لسنوات طويلة، حيث تتميز بمقاومتها للتصبغات والمتانة العالية.",
                },
              },
            ],
          })}
        </script>
      </Helmet>

      <h1 className="mb-4 text-center" style={{ color: "#00b3ff" }}>
        القشور الخزفية (الفينير)
      </h1>
      <h5 className="text-center mb-5">سر الابتسامة المثالية</h5>
      <p className="text-center text-muted">
        مركز سليم سمايل – رام الله | فن التجميل في طب الأسنان
      </p>

      <h2 className="mt-4">ما هي القشور الخزفية (Veneers)؟</h2>
      <p>
        القشور الخزفية – أو الفينير – هي عدسات تجميلية رقيقة تُصنّع خصيصًا
        لتُغطى بها السطح الأمامي للأسنان. تُستخدم لتحسين الشكل، اللون، والحجم،
        وتصحيح العديد من العيوب التجميلية التي قد تؤثر على الابتسامة.
      </p>
      <p>
        الفينير هو الحل الأمثل للحصول على{" "}
        <strong>الابتسامة الهوليودية الطبيعية والمتناسقة</strong>.
      </p>

      <h2 className="mt-4">متى نلجأ للقشور الخزفية؟</h2>
      <ul>
        <li>الأسنان المتصبغة التي لا تستجيب للتبييض.</li>
        <li>الفجوات بين الأسنان (الفراغات).</li>
        <li>الأسنان غير المتناسقة في الحجم أو الشكل.</li>
        <li>الأسنان المتآكلة أو المكسورة جزئيًا.</li>
        <li>تصحيح الابتسامة “اللثوية” عند الضرورة.</li>
      </ul>

      <h2 className="mt-4">أنواع القشور الخزفية في مركز سليم سمايل</h2>

      <h3 className="mt-3">1. فينير البورسلان (Porcelain Veneers)</h3>
      <ul>
        <li>متينة وتدوم طويلاً بمظهر شديد الواقعية.</li>
        <li>مقاومة للتصبغات.</li>
      </ul>

      <h3 className="mt-3">2. فينير الإيماكس (E-Max)</h3>
      <ul>
        <li>شفافية عالية ولمعان طبيعي.</li>
        <li>مثالي للأسنان الأمامية.</li>
      </ul>

      <h3 className="mt-3">3. فينير الزركونيا (Zirconia Veneers)</h3>
      <ul>
        <li>متين جدًا، يُستخدم للحالات التي تحتاج دعمًا إضافيًا.</li>
      </ul>

      <h2 className="mt-4">خطوات العلاج</h2>
      <ol>
        <li>الاستشارة والتخطيط الرقمي.</li>
        <li>تحضير خفيف للسطح الأمامي للأسنان (أو بدون تحضير).</li>
        <li>أخذ القياسات الرقمية أو التقليدية.</li>
        <li>تصميم وتصنيع القشور بالخزف عالي الجودة.</li>
        <li>تثبيت القشور بدقة باستخدام مواد لاصقة خاصة.</li>
      </ol>

      <h2 className="mt-4">مميزات الفينير التجميلي</h2>
      <ul>
        <li>ابتسامة بيضاء، موحّدة وجذابة.</li>
        <li>يحافظ على بنية السن الأصلية.</li>
        <li>مقاوم للتصبغات مع الأطعمة والمشروبات.</li>
        <li>غير مؤلم عند تنفيذه بشكل صحيح.</li>
        <li>نتائج تدوم لسنوات مع العناية المناسبة.</li>
      </ul>

      <h2 className="mt-4">العناية بعد الفينير</h2>
      <ul>
        <li>تفريش الأسنان مرتين يوميًا.</li>
        <li>استخدام الخيط بانتظام.</li>
        <li>تجنّب قضم الأشياء الصلبة مثل الجليد أو الأقلام.</li>
        <li>إجراء زيارات متابعة منتظمة للطبيب.</li>
      </ul>

      <h2 className="mt-4">رسالة مركز سليم سمايل</h2>
      <p>
        <em>
          "نؤمن أن الابتسامة المثالية تبدأ من التفاصيل الدقيقة. القشور الخزفية
          ليست فقط حلاً تجميليًا، بل تعبير عن الثقة والجمال بأسلوب راقٍ وطبي
          متكامل."
        </em>
      </p>
      <footer className="mt-4">
        <HashLink to="/#services" className="btn btn-outline-secondary">
          العودة للخدمات
        </HashLink>
      </footer>
    </div>
  );
};

export default Veneers;
