import React from "react";
import { motion } from "framer-motion";
import { Helmet } from "react-helmet-async";

function Insurance() {
  const insuranceLogos = [
    "/assets/albaraka.png",
    "/assets/almash.png",
    "/assets/alwatania.webp",
    "/assets/golp.jpeg",
    "/assets/med.png",
    "/assets/nathealth.webp",
    "/assets/palstine.png",
    "/assets/wecare.webp",
  ];

  return (
    <div className="insurance py-5 mb-5 overflow-hidden" id="insurance">
      {/*  Helmet SEO */}
      <Helmet>
        <title>شركات التأمين | مركز سليم سمايل – رام الله</title>
        <meta
          name="description"
          content="يتعاون مركز سليم سمايل مع مجموعة من أفضل شركات التأمين لتسهيل خدمات طب الأسنان وتوفير راحة أكبر لعملائنا."
        />
        <meta
          name="keywords"
          content="شركات التأمين, مركز سليم سمايل, رام الله, تأمين طبي, خدمات الأسنان"
        />
        <meta property="og:title" content="شركات التأمين | مركز سليم سمايل" />
        <meta
          property="og:description"
          content="تعرف على شركات التأمين المتعاقدة مع مركز سليم سمايل في رام الله."
        />
        <meta
          property="og:image"
          content="https://slimsmile.ps/images/insurance.jpg"
        />
        <meta property="og:type" content="website" />
        <meta property="og:locale" content="ar_AR" />
      </Helmet>

      {/* UI Content */}
      <div className="head text-center">
        <h2 className="mb-4 fs-1 fw-bolder" style={{ lineHeight: "1.8" }}>
          شركائنا
        </h2>

        <div className="smile-container">
          <svg
            viewBox="0 0 200 100"
            xmlns="http://www.w3.org/2000/svg"
            style={{ width: "140px", marginTop: "-50px" }}
          >
            <defs>
              <linearGradient
                id="lineGradient"
                x1="0%"
                y1="0%"
                x2="100%"
                y2="0%"
              >
                <stop offset="0%" stopColor="#2BA3DF" />
                <stop offset="100%" stopColor="#6dd5ed" />
              </linearGradient>
            </defs>
            <path
              d="M10,40 Q50,90 190,30"
              stroke="url(#lineGradient)"
              strokeWidth="8"
              fill="none"
              strokeLinecap="round"
            />
          </svg>
        </div>
      </div>
      <div className="container mt-3">
        <p className="text- mt-3 text-muted">
          يقدم مركز سليم سمايل العلاجات بالمشاركه مع شركات التامين التاليه:
        </p>
        <div className="row justify-content-center align-items-center g-3">
          {insuranceLogos.map((logo, index) => (
            <motion.div
              key={index}
              className="col-3 col-md-3 text-center"
              initial={{ opacity: 0, y: 50 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
            >
              <img
                src={logo}
                alt={`Insurance ${index + 1}`}
                className="img-fluid p-2 rounded-4 shadow-sm"
                loading="lazy"
                decoding="async"
                style={{ maxHeight: "100px", objectFit: "contain" , maxWidth :"60px" }}
              />
            </motion.div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default Insurance;
